import React, { Component } from 'react';
import { Table, Button, Icon } from 'antd';
import { setStudentForGraduation } from 'dump/data';

class TableQualifiedToGraduate extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Chuyên ngành',
        dataIndex: 'majors',
        key: 'majors',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'class',
        key: 'class',
      },
      {
        title: 'TBCHT TK',
        dataIndex: 'tBCHTTK',
        key: 'tBCHTTK',
      },
      {
        title: 'TBCTN',
        dataIndex: 'tBCTN',
        key: 'tBCTN',
      },
      {
        title: 'Xếp hạng',
        dataIndex: 'rankGraduation',
        key: 'rankGraduation',
      },
      {
        title: 'Kỳ 1',
        dataIndex: 'semesterScoreOne',
        key: 'semesterScoreOne',
      },
      {
        title: 'Kỳ 2',
        dataIndex: 'semesterScoreTwo',
        key: 'semesterScoreTwo',
      },
      {
        title: 'Kỳ 3',
        dataIndex: 'semesterScoreThree',
        key: 'semesterScoreThree',
      },
      {
        title: 'Kỳ 4',
        dataIndex: 'semesterScoreFour',
        key: 'semesterScoreFour',
      },
      {
        title: 'Kỳ 5',
        dataIndex: 'semesterScoreFive',
        key: 'semesterScoreFive',
      },
      {
        title: 'Ghi chú',
        dataIndex: 'note',
        key: 'note',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: () => (
          <Button type="primary" onClick={this.showModal}>
            Xem
          </Button>
        ),
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <div>
        <Button type="primary" className="btnSetDecisionNumber">
          <Icon type="export" /> Chuyển chưa tốt nghiệp
        </Button>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setStudentForGraduation}
          columns={this.columns}
        />
      </div>
    );
  }
}

export default TableQualifiedToGraduate;



